import React from 'react';

const HSV2RGB = (H, S, V) => {
    // Color converter library lifted from http://www.easyrgb.com/en/math.php
    //H, S and V input range = 0 รท 1.0
    //R, G and B output range = 0 รท 255

    let var_h = H * 6
    if ( var_h === 6 ) var_h = 0      //H must be < 1
    let var_i = Math.floor( var_h )             
    let var_1 = V * ( 1 - S )
    let var_2 = V * ( 1 - S * ( var_h - var_i ) )
    let var_3 = V * ( 1 - S * ( 1 - ( var_h - var_i ) ) )
    let var_r=0, var_g=0, var_b=0;

    if      ( var_i === 0 ) { var_r = V     ; var_g = var_3 ; var_b = var_1 }
    else if ( var_i === 1 ) { var_r = var_2 ; var_g = V     ; var_b = var_1 }
    else if ( var_i === 2 ) { var_r = var_1 ; var_g = V     ; var_b = var_3 }
    else if ( var_i === 3 ) { var_r = var_1 ; var_g = var_2 ; var_b = V     }
    else if ( var_i === 4 ) { var_r = var_3 ; var_g = var_1 ; var_b = V     }
    else                    { var_r = V     ; var_g = var_1 ; var_b = var_2 }

    let R = Math.floor(var_r * 255).toString(16)
    let G = Math.floor(var_g * 255).toString(16)
    let B = Math.floor(var_b * 255).toString(16)
    if (R.length===1) R = '0'+R;
    if (G.length===1) G = '0'+G;
    if (B.length===1) B = '0'+B;
    return '#' + R + G + B;
}

const styledTD = (id, min, max, block) => {
    const hue = (id - min + 0.5) / (max-min+1);
    const style = {
        "width":block+"px",
        "height":block+"px",
        "backgroundColor": (id===0 ? '#ffffff' : HSV2RGB(hue, 0.8, 0.8))
    }
    return <td style={style}> </td> 
}

const tablestyle = {
    "border-style":"solid", 
    "border-width": "1px"
}

const Grid = props => {
    //console.log(JSON.stringify(props));
    const minPiece = Math.min.apply(Math, props.pieces.map(i => i.id));
    const maxPiece = Math.max.apply(Math, props.pieces.map(i => i.id));
    if (typeof props.grid === 'undefined') {
        return <div><h1>Welcome to REACTRIS</h1></div>
    }
    else {
        return <table style={tablestyle}><tbody>
            {props.grid.map(row => <tr>{row.map(cell => styledTD(cell, minPiece, maxPiece, props.blocksize))}</tr>)}
        </tbody></table>
    }

};

export default Grid;