import React, { Component } from 'react';
//import axios from 'axios';
import Grid from '../Grid/Grid';
import Modal from '../Modal/Modal';

class Game  extends Component {
    constructor() {
        super();
        this.state = {
            pieceDb: undefined,
            gridWidth: 10,
            gridHeight: 30,
            playing: false,
            activeset: "4",
            activepiece: undefined,
            activerot: 0,
            gamespeed: 500,
            board: undefined,
            grabLocX: -1,
            grabLocY: -1,
            score: 0,
            rowcount: 0,
            speeduprow: 5,
            speedupfac: 0.75,
            blocksize: 15,
        }
    }
    componentDidMount() {
        //console.log('componentDidMount - ' + this.state)
        /*
        if (typeof this.state.pieceDb === 'undefined') {
            axios.get('http://localhost:4321/shape').then(response => {
                //console.log(response);
                this.setState({pieceDb: response.data});
                document.addEventListener("keydown", this.handleKeyPress);
            }).catch(err => alert('Server connection failed'));
        }
        */
        this.setState({pieceDb: [{"id":1,"grp":"4","x":2,"y":2,"shape":"aa/aa"},{"id":2,"grp":"4","x":3,"y":2,"shape":"aa./.aa"},{"id":3,"grp":"4","x":3,"y":2,"shape":".aa/aa."},{"id":4,"grp":"4","x":4,"y":1,"shape":"aaaa"},{"id":5,"grp":"4","x":3,"y":2,"shape":"a../aaa"},{"id":6,"grp":"4","x":3,"y":2,"shape":"..a/aaa"},{"id":7,"grp":"4","x":3,"y":2,"shape":"aaa/.a."},{"id":8,"grp":"5","x":5,"y":1,"shape":"aaaaa"},{"id":9,"grp":"5","x":3,"y":3,"shape":".aa/aa./.a."},{"id":10,"grp":"5","x":3,"y":3,"shape":"aa./.aa/.a."},{"id":11,"grp":"5","x":4,"y":2,"shape":"a.../aaaa"},{"id":12,"grp":"5","x":4,"y":2,"shape":"...a/aaaa"},{"id":13,"grp":"5","x":3,"y":2,"shape":".aa/aaa"},{"id":14,"grp":"5","x":3,"y":2,"shape":"aa./aaa"},{"id":15,"grp":"5","x":4,"y":2,"shape":"aaa./..aa"},{"id":16,"grp":"5","x":4,"y":2,"shape":".aaa/aa.."},{"id":17,"grp":"5","x":3,"y":3,"shape":"aaa/.a./.a."},{"id":18,"grp":"5","x":3,"y":2,"shape":"a.a/aaa"},{"id":19,"grp":"5","x":3,"y":3,"shape":"..a/..a/aaa"},{"id":20,"grp":"5","x":3,"y":3,"shape":"..a/.aa/aa."},{"id":21,"grp":"5","x":3,"y":3,"shape":".a./aaa/.a."},{"id":22,"grp":"5","x":4,"y":2,"shape":".a../aaaa"},{"id":23,"grp":"5","x":4,"y":2,"shape":"..a./aaaa"},{"id":24,"grp":"5","x":3,"y":3,"shape":".aa/.a./aa."},{"id":25,"grp":"5","x":3,"y":3,"shape":"aa./.a./.aa"},{"id":26,"grp":"3","x":3,"y":1,"shape":"aaa"},{"id":27,"grp":"3","x":3,"y":1,"shape":"aaa"},{"id":28,"grp":"3","x":2,"y":2,"shape":"aa/a."},{"id":29,"grp":"3","x":2,"y":2,"shape":"aa/a."},{"id":30,"grp":"3","x":3,"y":2,"shape":"aa./..a"},{"id":31,"grp":"3","x":3,"y":2,"shape":"..a/aa."},{"id":32,"grp":"3","x":3,"y":2,"shape":"a.a/.a."},{"id":33,"grp":"3","x":3,"y":3,"shape":"a../.a./..a"},{"id":34,"grp":"3","x":3,"y":3,"shape":"a.a/.../.a."},{"id":35,"grp":"3","x":3,"y":3,"shape":".a./a../..a"},{"id":36,"grp":"6","x":5,"y":2,"shape":".a.../aaaaa"},{"id":37,"grp":"6","x":5,"y":2,"shape":"..a../aaaaa"},{"id":38,"grp":"6","x":5,"y":2,"shape":"aa.../.aaaa"},{"id":39,"grp":"6","x":4,"y":2,"shape":"aa../aaaa"},{"id":40,"grp":"6","x":4,"y":2,"shape":"a.a./aaaa"},{"id":41,"grp":"6","x":4,"y":2,"shape":"a..a/aaaa"},{"id":42,"grp":"6","x":4,"y":2,"shape":".aa./aaaa"},{"id":43,"grp":"6","x":4,"y":3,"shape":"a.../a.../aaaa"},{"id":44,"grp":"6","x":4,"y":3,"shape":".a../.a../aaaa"},{"id":45,"grp":"6","x":4,"y":3,"shape":"a.../aaaa/a..."},{"id":46,"grp":"6","x":4,"y":3,"shape":"a.../aaaa/.a.."},{"id":47,"grp":"6","x":4,"y":3,"shape":"a.../aaaa/..a."},{"id":48,"grp":"6","x":4,"y":3,"shape":"a.../aaaa/...a"},{"id":49,"grp":"6","x":4,"y":3,"shape":".a../aaaa/..a."},{"id":50,"grp":"6","x":4,"y":3,"shape":".a../aaaa/.a.."},{"id":51,"grp":"6","x":4,"y":3,"shape":".a../aa../.aaa"},{"id":52,"grp":"6","x":4,"y":2,"shape":"aa.a/.aaa"},{"id":53,"grp":"6","x":5,"y":2,"shape":"aaa../..aaa"},{"id":54,"grp":"6","x":4,"y":2,"shape":"aaa./.aaa"},{"id":55,"grp":"6","x":3,"y":2,"shape":"aaa/aaa"},{"id":56,"grp":"6","x":4,"y":3,"shape":"aa../.aaa/.a.."},{"id":57,"grp":"6","x":3,"y":3,"shape":"aa./aaa/a.."},{"id":58,"grp":"6","x":4,"y":3,"shape":"aa../.aaa/..a."},{"id":59,"grp":"6","x":4,"y":3,"shape":"aa../.a../.aaa"},{"id":60,"grp":"6","x":4,"y":3,"shape":"a.../aa../.aaa"},{"id":61,"grp":"6","x":3,"y":3,"shape":"aa./a../aaa"},{"id":62,"grp":"6","x":3,"y":3,"shape":"aa./.a./aaa"},{"id":63,"grp":"6","x":3,"y":3,"shape":"aa./.aa/aa."},{"id":64,"grp":"6","x":4,"y":3,"shape":"a.../aaa./..aa"},{"id":65,"grp":"6","x":3,"y":3,"shape":"..a/.aa/aaa"},{"id":66,"grp":"6","x":3,"y":3,"shape":".a./aaa/.aa"},{"id":67,"grp":"6","x":3,"y":3,"shape":"aa./.aa/.aa"},{"id":68,"grp":"6","x":4,"y":3,"shape":"aa../.aa./..aa"},{"id":69,"grp":"6","x":6,"y":1,"shape":"aaaaaa"},{"id":70,"grp":"6","x":5,"y":2,"shape":"a..../aaaaa"}]});
        document.addEventListener("keydown", this.handleKeyPress);
    }
    
    calcSpeedUp = () => {
        let r = this.state.rowcount;
        let s = this.state.gamespeed;
        const L = this.state.speeduprow;
        const f = this.state.speedupfac;
        while (r>L) {
            r-=L;
            s*=f;
        }
        return s;
    }

    pickNewPiece = () => {
        const possiblePieces = this.state.pieceDb.filter(i => i.grp===this.state.activeset).map(i => i.id);
        const minPiece = Math.min.apply(Math, possiblePieces);
        const maxPiece = Math.max.apply(Math, possiblePieces);
        const randPiece = Math.floor(Math.random() * (maxPiece-minPiece+1)) + minPiece;
        this.setState({activepiece: randPiece, activerot: 0, grabLocX: Math.floor(this.state.gridWidth/2), grabLocY: 0});
        if (!this.handleFallingPiece(2, this.state.grabLocX, this.state.grabLocY)) {
            // can't fit new piece at the top of the board? must mean game over
            this.setState({playing: false});
            alert('Game Over!');
        }
        this.clearCompletedRows();
    }

    computeRotation = (shape, r) => {
        if (r===0) return shape;
        const rows = shape.length;
        const cols = shape[0].length;
        let newshape = [];
        for (let i=0; i<cols; i++) {
            let newrow = '';
            for (let j=0; j<rows; j++) {
                newrow+=shape[rows-j-1][i];
            }
            newshape.push(newrow);
        }
        return this.computeRotation(newshape, r-1);
    }

    handleFallingPiece = (flag, nx, ny) => {
        // flag=0: remove piece
        // flag=1: draw piece
        // flag=2: don't draw, instead return boolean depending on whether (nx, ny) is a legal position for the piece
        if (this.state.activepiece===undefined) return true;
        const unrotatedPiece = this.state.pieceDb.filter(p => p.id===this.state.activepiece)[0];
        const shape = this.computeRotation(unrotatedPiece.shape.split('/'), this.state.activerot);
        const anchorX = Math.floor(unrotatedPiece.x/2);
        const anchorY = Math.floor(unrotatedPiece.y/2);
        let newboard = this.state.board.map(a => a.slice()); // 2d array copy, need to be careful of reference/value
        const dim1 = (this.state.activerot%2 === 0) ? unrotatedPiece.x : unrotatedPiece.y;
        const dim2 = (this.state.activerot%2 === 0) ? unrotatedPiece.y : unrotatedPiece.x;
        
        for (let i=0; i<dim1; i++) {
            for (let j=0; j<dim2; j++) {
                if (shape[j][i]!=='.') {
                    const xc = nx+i-anchorX;
                    const yc = ny+j-anchorY;
                    if (yc<0) continue; // above the grid isn't illegal, just don't draw it
                    if (xc<0 || xc>=this.state.gridWidth || yc>=this.state.gridHeight) {
                        if (flag===2) return false;
                        else continue;
                    }
                    if (flag===1) 
                        newboard[yc][xc]=this.state.activepiece;
                    else if (flag===0)
                        newboard[yc][xc]=0;
                    else if (flag===2) 
                        if (newboard[yc][xc]!==0) return false;
                }
            }
        }
        if (flag===2) return true;
        else this.setState({board: newboard});


    }

    tick = () => {
        if (this.state.playing) {
            //console.log('tick!');
            this.handleFallingPiece(0, this.state.grabLocX, this.state.grabLocY);
            // attempt to apply gravity by one square
            if (!this.handleFallingPiece(2, this.state.grabLocX, this.state.grabLocY+1)) {
                // lock piece
                this.handleFallingPiece(1, this.state.grabLocX, this.state.grabLocY);
                this.pickNewPiece();
            }
            else {
                this.setState({grabLocY: this.state.grabLocY+1});
                this.handleFallingPiece(1, this.state.grabLocX, this.state.grabLocY);
            }
            setTimeout(this.tick, this.calcSpeedUp());
        }
    }

    setupQuestions = {
        "Block Set (3, 4, 5, 6)": "4",
        "Tick speed (ms)": 200,
        "Grid Width": 10,
        "Grid Height": 30,
        "Speedup every n rows": 5,
        "By this factor": 0.75,
        "Block Size (pixels)": 15,
    };

    startGame = () => {
        if (this.state.playing) {
            this.setState({playing: false});  
        }
        else {
            const userInputs = document.getElementsByTagName('input');
            const boardWidth = Number(userInputs[2].value);
            const boardHeight =Number(userInputs[3].value);


            const emptyCell = 0;
            let emptyRow = [];
            let emptyBoard = [];
            for (let i=0; i<boardWidth; i++)
                emptyRow.push(emptyCell);
            for (let i=0; i<boardHeight; i++)
                emptyBoard.push(emptyRow);

            // start game is going to reproduce the pick-new-piece logic (minus game over) to avoid race conditions with multiple state updates
            const possiblePieces = this.state.pieceDb.filter(i => i.grp===userInputs[0].value).map(i => i.id);
            const minPiece = Math.min.apply(Math, possiblePieces);
            const maxPiece = Math.max.apply(Math, possiblePieces);
            const randPiece = Math.floor(Math.random() * (maxPiece-minPiece+1)) + minPiece;
            this.setState({
                activepiece: randPiece, 
                activerot: 0, 
                grabLocX: Math.floor(boardWidth/2), 
                grabLocY: 0, 
                playing: true, 
                board: emptyBoard,
                // populate config with data from modal - these probably should be namified
                activeset: userInputs[0].value,
                gamespeed: Number(userInputs[1].value),
                gridWidth: boardWidth,
                gridHeight:boardHeight,
                speeduprow: Number(userInputs[4].value),
                speedupfac: Number(userInputs[5].value),
                rowcount: 0,
                blocksize: Number(userInputs[6].value),
            });
            setTimeout(this.tick, this.calcSpeedUp());
        }
    } 

    clearCompletedRows = () => {
        // todo: make shinier css
        const emptyCell=0;
        let emptyRow = [];
        let fullRows = [];
        for (let i=0; i<this.state.gridWidth; i++)
            emptyRow.push(emptyCell);
        for (let i=0; i<this.state.gridHeight; i++) {
            let rowFull = true;
            for (let j=0; j<this.state.gridWidth; j++) {
                if (this.state.board[i][j]===0) rowFull=false;
            }
            if (rowFull) fullRows.push(i);
        }
        if (fullRows.length>0) {
            let newboard = [];
            for (let i=0; i<fullRows.length; i++) {
                newboard.push(emptyRow);
            }
            for (let i=0; i<this.state.gridHeight; i++) {
                if (fullRows.indexOf(i)<0)
                    newboard.push([...this.state.board[i]]);
            }
            this.setState({board: newboard, rowcount: this.state.rowcount+fullRows.length});
        }

    }

    tryMove = left => {
        const xd = left ? -1 : 1;
        this.handleFallingPiece(0, this.state.grabLocX, this.state.grabLocY);
        if (this.handleFallingPiece(2, this.state.grabLocX+xd, this.state.grabLocY)) {
            this.setState({grabLocX: this.state.grabLocX+xd});
        }
        this.handleFallingPiece(1, this.state.grabLocX, this.state.grabLocY);

    }

    tryRotate = () => {
        // todo
        this.handleFallingPiece(0, this.state.grabLocX, this.state.grabLocY);
        this.setState({activerot: (this.state.activerot+1)%4});
        if (!this.handleFallingPiece(2, this.state.grabLocX, this.state.grabLocY)) {
            // can't rotate, something's in the way, put it back
            this.setState({activerot: (this.state.activerot+3)%4});
        }
        this.handleFallingPiece(1, this.state.grabLocX, this.state.grabLocY);

    }

    dropPiece = () => {
        this.handleFallingPiece(0, this.state.grabLocX, this.state.grabLocY);
        for (let yd=1; yd<this.state.gridHeight; yd++) {
            if (!this.handleFallingPiece(2, this.state.grabLocX, this.state.grabLocY+yd)) {
                yd--; // last resting place
                this.setState({grabLocY: this.state.grabLocY+yd});
                break;
            }
        }
        this.handleFallingPiece(1, this.state.grabLocX, this.state.grabLocY);
        this.pickNewPiece();
    }

    handleKeyPress = e => {
        const keyCode = e.keyCode;
        if (this.state.playing) {
            //console.log('keypress! ' + keyCode);
            if (keyCode===65 || keyCode===37 || keyCode===100) {
                // left
                this.tryMove(true);
            }
            if (keyCode===68 || keyCode===39 || keyCode===102) {
                // right
                this.tryMove(false);
            }
            if (keyCode===87 || keyCode===38 || keyCode===104) {
                // up
                this.tryRotate();
            }
            if (keyCode===83 || keyCode===40 || keyCode===98 || keyCode===101 || keyCode===12) {
                // down
                this.dropPiece();
            }
        }
        else {
            //console.log('ignored ' + keyCode);
        }
    }





    render() {
        return <div > 
            <center>
            <Grid
                pieces={this.state.pieceDb ? this.state.pieceDb.filter(i => i.grp===this.state.activeset) : []}
                grid={this.state.board}
                blocksize={this.state.blocksize}>
            </Grid>
            {this.state.playing ? null : <Modal question={this.setupQuestions}></Modal>}
            {this.state.playing ? "Row Count: " + this.state.rowcount : null}
            <br></br>

            <button onClick={this.startGame}>{this.state.playing ? 'Stop Game' : 'Start Game'}</button>
            </center>
        </div>
    }
}

export default Game;